import { Injectable } from '@angular/core';
import { HttpService } from './http/http.service';

@Injectable({
  providedIn: 'root'
})
export class AccountControllerService {
  account = "Account/";
  student = "Student/";

  constructor(private http:HttpService) { }

  Add(model: any) {
    return this.http.httpPost(this.account  + 'studentRegister/', model);
  }
  AddByAdmin(model: any) {
    return this.http.httpPost(this.account  + 'studentRegisterByAdmin/', model);
  }
  
  AddManager(model: any) {
    return this.http.httpPost(this.account  + 'ManagerRegister/', model);
  }
  AddTeacher(model: any) {
    return this.http.httpPost(this.account  + 'TeacherRegister/', model);
  }
  Update(model: any) {
    return this.http.HttpPost(this.student  + 'update/', model);
  }
  GetPermissions() {
    return this.http.httpGet(this.account + "GetPermissions");
  }
}
